.about-gallery {
  background-color: #fff;
  padding: 50px 0 80px;
  position: relative;
}

.about-title {
  font-size: 36px;
  line-height: 50px;
  max-width: 570px;
  margin: 0 auto;
  margin-bottom: 55px;
  text-align: center;
}

.about-gallery_wrap {
  display: flex;
  height: 540px;
  justify-content: space-between;
  position: relative;
}

.about-gallery_wrap a {
  display: block;
  height: 100%;
  width: 31%;
  background-color: #D8D8D8;
  border-radius: 4px;
  overflow: hidden;
}

.about-gallery_wrap a:hover img {
  transform: scale(1.1);
}

.about-gallery_wrap a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.about-gallery_wrap a:nth-child(2), .about-gallery_wrap a:nth-child(3) {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 31%;
  height: 255px;
}

.about-gallery_wrap a:nth-child(2) {
  top: 0;
}

.about-gallery_wrap a:nth-child(3) {
  bottom: 0;
}

.about-gallery_wrap a:nth-child(n+5) {
  display: none;
}

#viewbox-button {
  display: block;
  width: 105px;
  height: 40px;
  background-color: #fff;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.24);
  border-radius: 12px;
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  color: #1F283C;
  line-height: 20px;
  padding: 10px 0;
  position: absolute;
  right: 20px;
  bottom: 20px;
  top: unset;
  z-index: 5;
}

#about-gallery_slider {
  display: none;
}

.owl-custom_counter {
  display: none;
}

@media screen and (max-width: 990px) {
  .about-gallery_wrap {
    height: 340px;
  }
  .about-gallery_wrap a:nth-child(2), .about-gallery_wrap a:nth-child(3) {
    height: 160px;
  }
}

@media screen and (max-width: 500px) {
  .about-gallery {
    padding: 25px 0 30px;
  }
  .about-title {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 25px;
  }
  .about-gallery_wrap {
    display: none;
  }
  #about-gallery_slider {
    display: block;
  }
  #about-gallery_slider .owl-nav, #about-gallery_slider .owl-dots {
    display: none;
  }
  #about-gallery_slider .slider-item {
    height: 290px;
  }
  #about-gallery_slider .slider-item a {
    display: block;
    width: 100%;
    height: 100%;
  }
  #about-gallery_slider .slider-item a img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .owl-custom_counter {
    display: flex;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    padding: 4px 12px;
    border-radius: 4px;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.55);
    position: absolute;
    right: 20px;
    bottom: 50px;
    z-index: 5;
  }
  .owl-custom_counter .separator {
    margin: 0 3px;
  }
  .owl-custom_counter--single {
    bottom: 20px;
  }
}

